  //Se usan         - 8114189569 prueba 3G1J85AC4DS525380
  async getSinCita(data){

    let dataToken = "";
    let tokenStorage = "";

    if(localStorage.getItem("token") === null){

      let tokensito = await this.getToken();
      localStorage.setItem("token", JSON.stringify(tokensito));
      dataToken = tokensito;
      //console.log("Obtenemos token por primera vez");

    } else {
      //console.log("Tenemos token pero checamos si expiro")
      tokenStorage = JSON.parse(localStorage.getItem("token")).access_token
      const jwtPayload = JSON.parse(window.atob(tokenStorage.split('.')[1]))
      const isExpired = Date.now() >= jwtPayload.exp * 1000;
      //console.log("Vacio", isExpired)
      
      if(isExpired === true){
        //Si expira el token, vuelve consulta uno nuevo.
        let tokensito = await this.getToken();
        localStorage.setItem("token", JSON.stringify(tokensito));
        dataToken = tokensito;
      }
    }

    const params = {
      datos: data.data,
      token: JSON.parse(localStorage.getItem("token")).access_token || dataToken,
      url: data.url,
    }
    //console.log("Token: ", params)
    if(data.url === "vin"){ //3G1J85AC4 DS525380
      let vinCarro = await this.getVin(params);
      //console.log("Estamos en el vin", vinCarro);

      if(vinCarro.data.message === "Carro encontrado") {
        //return vinCarro.data.cars;

        const parametros = {
          token: JSON.parse(localStorage.getItem("token")).access_token || dataToken,
          idCliente: vinCarro.data.cars[0].clientid,
        }
  
        let idClientes = await this.getClienteId(parametros);

        const array = {
          id: idClientes.data[0].id,
          codigo: idClientes.data[0].code,
          datosCliente: idClientes.data[0].clientInfo,
          data: vinCarro.data.cars[0],
          direccionCliente: idClientes.data[0].clientAddress,
          contactoCliente: idClientes.data[0].clientContact
        }

        //console.log("Estamos en el vin", idClientes);

        return array;
      } else {
        return 3
      }

    } else {
      let cliente = await this.getBuscarCliente(params);
      //console.log("Estamos en el clientes: ", cliente);
      if(cliente.message === "Cliente encontrado"){
        console.log("Tengo el dato del cliente: ", cliente.data);

        const array = {
          id: cliente.data[0].id,
          codigo: cliente.data[0].code,
          datosCliente: cliente.data[0].clientInfo,
          CarrosCliente: cliente.data[0].clientCars,
          direccionCiente: cliente.data[0].clientAddress,
          contactoCliente: cliente.data[0].clientContact
        }

        return array;


      } else {
        //console.log("Cliente no encontrado, error", cliente.data.message);
        if(data.url === "phone"){
          return 2;
        } else {
          return 1
        }
      }
    }

  }

