  async getConCita(data){
    //console.log(data);
    let dataToken = "";
    let tokenStorage = "";

    if(localStorage.getItem("token") === null){

      let tokensito = await this.getToken();
      localStorage.setItem("token", JSON.stringify(tokensito));
      dataToken = tokensito;
      //console.log("Obtenemos token por primera vez");

    } else {
      //console.log("Tenemos token pero checamos si expiro")
      tokenStorage = JSON.parse(localStorage.getItem("token")).access_token
      const jwtPayload = JSON.parse(window.atob(tokenStorage.split('.')[1]))
      const isExpired = Date.now() >= jwtPayload.exp * 1000;
      //console.log("Vacio", isExpired)
      
      if(isExpired === true){
        //Si expira el token, vuelve consulta uno nuevo.
        let tokensito = await this.getToken();
        localStorage.setItem("token", JSON.stringify(tokensito));
        dataToken = tokensito;
      }
    }

    const date = new Date();
    const [month, day, year] = [date.getMonth() + 1, date.getDate(), date.getFullYear()];
    //console.log("TOken: ", JSON.parse(localStorage.getItem("token")).access_token || dataToken); 81 1080 8942

    const params = {
      token: JSON.parse(localStorage.getItem("token")).access_token|| dataToken,
      date: `${year}-${month}-${day}`,
      agency: 1043193
    }

    let ordenesdia = await this.getOrdenesDia(params);

    let resultadoFilter = "";
    //console.log("dia: ", params.date)
    if(data.medio === "whatsapp"){
      //console.log("Estamos en whatsApp", ordenesdia.data);

      const rfnFilter = ordenesdia.data.filter((obj) => obj.car.id > 0);

      const result = rfnFilter.filter((obj) => obj.client.phone === data.campo);
      //console.log("result", result);
      resultadoFilter = result;

    } else if(data.medio === "correo") {
      //console.log("Estamos en el correo", ordenesdia);
      const rfnFilter = ordenesdia.data.filter((obj) => obj.car.id > 0);

      const result = rfnFilter.filter((obj) => {
        let min = obj.client.mail.toLowerCase()
        //console.log(min);
        return min === data.campo
      });
      resultadoFilter = result;

    } else if(data.medio === "vin") {
      //console.log("Estamos en el vin RF956084", ordenesdia.data); 

      const rfnFilter = ordenesdia.data.filter((obj) => obj.car.id > 0);

      const result = rfnFilter.filter((obj) => obj.car.matricul === data.campo);
      //console.log("Estamos en el vin", data.campo);
      resultadoFilter = result;
    }

    if(resultadoFilter.length === 0){
      return 5;
    } else {
      return resultadoFilter;
    }

  }

