    //Se usa
  async getEditarDatos(data){

    //console.log("Tengo: ", data);

    var datosFac = JSON.parse(localStorage.getItem("informacion"));

    var direccionConcat = `${data.Calle} ${data.Numero}, COL ${data.Colonia}, ${data.Municipio}, ${data.Estado}`;

    var myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${JSON.parse(localStorage.getItem("token")).access_token}`);
    myHeaders.append("Accept", "application/javascript");
    myHeaders.append("Content-Type", "application/json");

    var raw = JSON.stringify({
      "idMM": datosFac.id,
      "codigo": datosFac.codigo,
      "nombreCompleto": data.Nombre,
      "rfc": data.RFC,
      "correo": data.Correo,
      "direccion": direccionConcat,
      "tel": data.Telefono,
      "cel": data.Celular
    });

    var requestOptions = {
      method: 'PUT',
      headers: myHeaders,
      body: raw,
      redirect: 'follow'
    };

    let actualizar = await fetch("https://multimarca.gruporivero.com/api/v1/servicio/cliente", requestOptions);

    return actualizar.json();
    
  }

